<template>
    <div class="sec_7-box" v-if="posts">

        <div class="sec_7--item" v-for="(post,index) in posts" :key="index">
            <a href="#" class="sec_7--item_in w-inline-block">
                <img
                    src="../assets/images/Juliette-van-Rhyn-website.jpeg"
                    loading="lazy"
                    alt
                    class="sec_7--item-img"
                />
                <div class="sec_7--item_cont">
                    <p class="sec_7--item_date">
                        <span class="author_box">By Ahmad Eyamin</span> ‚Ä¢ {{new Date(post.date).toISOString().substring(0, 10)}}
                    </p>
                    <h1 class="sec_7-text">
                        {{post.title.rendered}}
                    </h1>
                    <p class="sec_7-readmore">Read More ...</p>
                </div>
            </a>
        </div>

    </div>
</template>

<script>

import axios from 'axios'
export default {
    name: 'MyBlogPosts',
    data(){
        return{
            posts: [],

        }
    },

    mounted(){
        axios.get('https://laraveldaily.com/wp-json/wp/v2/posts?per_page=3').then(success=>{
            this.posts = success.data
        })
    }
};
</script>

